import Vue from 'vue'
import VueRouter from 'vue-router'
import '../assets/css/header.css'
import '../assets/css/reset.css'
import shop from '../views/shop.vue'
import Home from '../views/home.vue'
import myPhone from '../views/myphone.vue'
import pageOne from '../views/page-one.vue'
import pageTwo from '../views/page-two.vue'
import pageThree from '../views/page-three.vue'
import pageFour from '../views/page-four.vue'
import pageFive from '../views/page-five.vue'

Vue.use(VueRouter)

var router = new VueRouter({
    routes: [
        {path:'/',redirect:'/Home'},
        {path:'/shop',component:shop},
        {path:'/Home',component:Home},
        {path:'/myPhone',component:myPhone},
        {path:'/pageOne',component:pageOne},
        {path:'/pageTwo',component:pageTwo},
        {path:'/pageThree',component:pageThree},
        {path:'/pageFour',component:pageFour},
        {path:'/pageFive',component:pageFive},
    ]
})

export default router